.common {
  text-align: left;
  line-height: 1.5;
  font-size: 2vw;
  color: #22BAD9; }
  @media only screen and (max-width: 500px) {
    .common {
      font-size: 6vw;
      padding: 10px 10px; } }
  @media only screen and (min-width: 500px) and (max-width: 1000px) {
    .common {
      font-size: 4vw;
      padding: 30px 50px; } }
  @media only screen and (min-width: 1000px) {
    .common {
      font-size: 2vw;
      padding: 50px 70px; } }
  .common .name {
    color: #FF5166; }
  .common a {
    color: #FF69B4; }
  .common .kotlin {
    color: #F18E33; }
  .common .java {
    color: #b07219; }
  .common .js {
    color: #f1e05a; }
  .common .dart {
    color: #00B4AB; }
  .common .others {
    color: rgba(255, 255, 255, 0.5); }
  .common .fan-of {
    margin-left: 10px; }
